<template>
  <div class="bottomNav">
    <ul>
        <li v-for="(tabNav,index) in navlist" :key="index"><router-link :to="tabNav.url">
          <i :class="tabNav.class"></i>
          <span>{{tabNav.name}}</span></router-link>
          
          </li>
    </ul>
  </div>
</template>


<script>

export default {
  components:{

  },
  data () {
    return {
      navlist:[{
        name:'首页',
        class:'el-icon-goods',
        url:'/home'
      },
      {
        name:'附近',
        class:'el-icon-view',
        url:'/close'
      },
      {
        name:'搜索',
        class:'el-icon-search',
        url:'/search'
      },
      {
       name:'订单',
       class:'el-icon-tickets',
       url:'/ticket'
      },
      {
       name:'我的',
       class:'el-icon-menu',
       url:'/mine'
      }]
    }
  },



}
</script>

<style lang="less">
.bottomNav{
  position: fixed;
  bottom: 0px;
    ul{
        width: 100%;
        height:80px;
        background: #e7e7e7;
        li{
            line-height: 80px;
            height:80px;
            float: left;
            width:150px;
            text-align:center;
            cursor: pointer;
            a{
                line-height: 80px;
                display: block;
                height:80px;
                width:150px;
                color:#333;
                position: relative;
                text-decoration: none;
                &.router-link-active{
                    color:#ff2d4b;
                }
                i{
                  display: block;
                  font-size:30px;
                  position: relative;
                  top:10px;
                }
                span{
                  display: block;
                  font-size:24px;
                  position: relative;
                  top:-8px;
                }
            }
            
        }
    }
}
</style>



