import Vue from 'vue'
import Router from 'vue-router'
import home from '@/view/home'
import close from '@/view/close'
import search from '@/view/search'
import ticket from '@/view/ticket'
import mine from '@/view/mine'
Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      redirect: '/home',
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/close',
      name: 'close',
      component: close
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/ticket',
      name: 'ticket',
      component: ticket
    },
    {
      path: '/mine',
      name: 'mine',
      component: mine
    }
  ]
})



