<template>
  <div class="home">
      <div class="banner">
        <swiper :options="swiperOption">
          <swiper-slide><img src="../assets/waterbanner.jpg"></swiper-slide>
          <swiper-slide><img src="../assets/waterbanner.jpg"></swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
      </div>
      <div class="homenav">
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="附近的店" name="first">
              <list-info :listmsg='listmsg'></list-info>
            </el-tab-pane>
            <el-tab-pane label="人气排名" name="second">
              <list-info :listmsg='listmsg2'></list-info>
            </el-tab-pane>
            <el-tab-pane label="领券排行" name="third">
                <div class="shoppic">
                    <img src="../assets/timg.jpg">
                    <p>暂无商户</p>
                </div>
            </el-tab-pane>
            <el-tab-pane label="免费入驻" name="fourth">
              <div class="merchant">
                <img src="../assets/waterlogo.png">
                <p>附近送水电话</p>
                <label><el-input v-model="input" placeholder="手机号"></el-input></label>
                <label class="msgnumber">
                  <el-input v-model="input" placeholder="短信验证码"></el-input>
                  <span>获取验证码</span>
                </label>
                <div class="phoneBtn">
                    <el-button type="warning">立即注册</el-button>
                    <el-button type="success">微信登陆</el-button>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
      </div>
      
  </div>
</template>

<script>
import Swiper from 'swiper'
import listInfo from '../public/components/listInfo'
export default {
  name: 'home',
  components:{
    listInfo
  },
  data () {
    return {
      activeName: 'first',
      listmsg:[],
      listmsg2:[],
      input:"",
      swiperOption: {
        loop: true,
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    }
  },
  created: function(){
    let self = this  
    // get 请求
    this.$axios.get('/api' + '?type=top&key=4a2a18ca10530d3ae5ff01a731251e96')  // url格式,如:?type=xxx&key=value
      .then(res => {
        this.listmsg=res.data.result.data;
        this.currentTotal = (res.data.result.data.length);
        //console.log(res.data.result.data.length); 
      }) 
      .catch(err => { 
        console.log(err); 
      })
       // get 请求
    this.$axios.get('/api' + '?type=shehui&key=4a2a18ca10530d3ae5ff01a731251e96')  // url格式,如:?type=xxx&key=value
      .then(res => {
        this.listmsg2=res.data.result.data;
        this.currentTotal = (res.data.result.data.length);
        //console.log(res.data.result.data.length); 
      }) 
      .catch(err => { 
        console.log(err); 
      })

  },
}
</script>

<style lang='less'>
.home{
  font-size:30px;
  .banner{
    img{
      width:100%;
    }
  }
  .homenav{
    margin-top:10px;
    padding-bottom:80px;
    #tab-first{
      padding-left:30px;
    }
    #tab-fourth{
      padding-right:30px;
    }
    .el-tabs__item{
      font-size:30px;
      height:60px;
      padding:10px 30px;
      color:#333;
    }
    .is-active{
      color:#ff2d4b;
    }
    .el-tabs__active-bar{
      position: absolute;
      bottom: 0;
      left: 0px;
      height: 0.026667rem;
      background-color: #ff2d4b;
      z-index: 1;
      display: none;  
    }
    .shoppic{
      img{
        margin: 0 auto;
        width:300px;
        display:block;
      }
      p{
        text-align: center;
        margin: 20px;
      }
    }
    .merchant{
      width: 500px;
      margin:0 auto;
      img{
        width: 200px;
        display: block;
        margin: 0 auto;
      }
      p{
        text-align: center;
        margin: 10px;
        font-size:24px;
      }
      .el-input__inner{
        height: 60px;
        line-height: 60px;
        margin-bottom: 40px;
        border:none;
        border-bottom: 1px solid #e0e0e0;
        
      }
      .msgnumber{
        position: relative;
        span{
          position: absolute;
          right: 0px;
          top:0px;
          font-size: 20px;
          color:#ff2d4b;
          height: 60px;
          line-height: 60px; 
        } 
      }
      .phoneBtn{
        text-align: center;
        .el-button{
          margin:0px 40px;
          border-radius:40px;
        }
      }
      
    }
  }
}
</style>



